// export default Filters;

import React from "react";

const Filters = ({ selectedFilters, setSelectedFilters }) => {
  const handleJobTypeChange = (e) => {
    const value = e.target.value.toLowerCase();
    setSelectedFilters((prev) => ({
      ...prev,
      jobType: prev.jobType.includes(value)
        ? prev.jobType.filter((type) => type !== value)
        : [...prev.jobType, value],
    }));
  };

  const handleSalaryChange = (e) => {
    setSelectedFilters((prev) => ({ ...prev, salaryRange: e.target.value }));
  };

  const handleExperienceChange = (e) => {
    setSelectedFilters((prev) => ({
      ...prev,
      experienceRange: e.target.value,
    }));
  };

  return (
    <div className="filters">
      <label>
        Job Type:
        <select multiple onChange={handleJobTypeChange}>
          <option value="full time">Full Time</option>
          <option value="part time">Part Time</option>
          <option value="contract">Contract</option>
        </select>
      </label>
      <label>
        Salary Range:
        <select onChange={handleSalaryChange}>
          <option value="">Any</option>
          <option value="40k-60k">40k-60k</option>
          <option value="60k-80k">60k-80k</option>
        </select>
      </label>
      <label>
        Experience Range:
        <select onChange={handleExperienceChange}>
          <option value="">Any</option>
          <option value="0-2">0-2 years</option>
          <option value="3-5">3-5 years</option>
        </select>
      </label>
    </div>
  );
};

export default Filters;
